/// Theme for tabset component.
///
/// @author Federico Gambardella<federico.gambardella@micegroup.it>
/// @param $section
/// @param $map
@mixin component-tabset($section, $map) {
    @if($section == 'tabset') {
        .fk-tabset {
            border-bottom: map-get($map, border-bottom); 

            li {
                color: map-get($map, li-color);
            }

            .fk-tab-link {
                color: map-get($map, tab-link-color);
            }

            .fk-active {

                &::before{
                    border-top: map-get($map, active-before-boreder-top);
                }
                
            }

            .hover {
                &:hover {

                    &:before {
                        border-top: map-get($map, hover-before);
                    }
                }
            }
        }

        .fk-tab {

            &:hover {

                .fk-tab-link {
                    color: map-get($map, tab-link-hover-color);
                }

                .icon-not-active {
                    color:  map-get($map,icon-not-active-hover-color);
                }

                .icon-active {
                    color:  map-get($map,icon-active-hover-color);
                }
                
            }
             
             &.fk-active {

                   .fk-tab-link {
                    background-color: transparent;
                    color: map-get($map,tab-link-active-color);
                   }
             }
        }

        .not-active {
            color: map-get($map,not-active);
        }
        
        .icon-active {
            color:  map-get($map,icon-active);;
        }
        
        .icon-not-active {
            color: map-get($map,icon-not-active);;
        }
    }
}



 















