$columns: 18; // Number of columns in the grid system

.fk-row {
	display: flex;
	flex-wrap: wrap;
	margin: 0 auto;
    flex-direction: row;
}



@for $width from 1 through $columns {
	.fk-col-#{$width} {
		flex-basis: $width / $columns * 100%;
		padding-right: 15px; //GUTTER
		max-width: $width / $columns * 100% ;
	}  
}


@media(max-width: 555px) {
    .fk-row {
      flex-direction: column;
    }
	@for $width from 1 through $columns {
		.fk-col-#{$width} {
            max-width: 100%;
			flex-basis: 100%;
		}
	}
}