@import '../utilities/map';

$sizing-props: (
    "w": "width",
    "h": "height",
    "mw": "max-width",
    "miw": "min-width",
    "mh": "max-height",
    "mih": "min-height",
);

$sizing-props-width: (
    "w": "width",
    "mw": "max-width",
    "miw": "min-width",
);

$sizing-props-height: (
    "h": "height",
    "mh": "max-height",
    "mih": "min-height",
);

$viewport: "v";
$percentage: "pr";


@mixin set-size($map, $name, $map-props) {
    @each $section, $value in $map {
        @each $section2, $value2 in $map-props {
            .fk-#{$name}-#{$value2}-#{$section} {
                #{$value2}: $value;
            }
        }
    }
}
