.fk-badge-content {
    position: absolute;
    text-align: center;
    display: inline-block;
    border-radius: 50%;
    transition: transform .2s ease-in-out;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    pointer-events: none;
    font-weight: 600;
    font-size: 12px;
    font-family: Roboto,Helvetica Neue,sans-serif;
    color: #fff;
  }
  

 .fk-badge {
    position:relative;
  }

.fk-badge-small {

   width: 22px;
   height: 22px;
   line-height: 22px;

      &.fk-badge-top-right {
         top: -11px;
         right: -11px;
      }
   
      &.fk-badge-top-left {
        top: -11px;
        left: -11px;
      }
 
      &.fk-badge-bottom-right {
         bottom: -11px;
         right: -11px;
      }
 
      &.fk-badge-bottom-left {
         bottom: -11px;
         left: -11px;
      }


      &.fk-badge-top-right-no-overlap {
         top: -11px;
      }
   
      &.fk-badge-top-left-no-overlap {
        top: -11px;
      }
 
      &.fk-badge-bottom-right-no-overlap {
         bottom: -11px;
         left: -22px
      }
 
      &.fk-badge-bottom-left-no-overlap {
         bottom: -11px;
         left: -22px
      }
 
}

.fk-badge-large {

   width: 28px;
   height: 28px;
   line-height: 28px;

   &.fk-badge-top-right {
      top: -14px;
   }

   &.fk-badge-top-left {
     top: -14px;
     left: -14px;
   }

   &.fk-badge-bottom-right {
      bottom: -14px;
      right: -14px;
   }

   &.fk-badge-bootm-left {
      bottom: -14px;
      left: -14px;
   }


   &.fk-badge-top-right-no-overlap {
      top: -14px;
   }

   &.fk-badge-top-left-no-overlap {
     top: -14px;
   }

   &.fk-badge-bottom-right-no-overlap {
      bottom: -14px;
      left: -28px
   }

   &.fk-badge-bootm-left-no-overlap {
      bottom: -14px;
      left: -28px;
   }


   
}

 