///Theme for component action.
///
///@author Federico Gambardella<federico.gambardella@micegroup.it>
///@param $section
///@param $map
@mixin component-action($section, $map) {
    @if($section == 'action' ) {
        .#{$section} {
            border-right: map-get($map, border);
        }

        .action-container {
            color: map-get($map, color );
        }
    }
}