.fk-tabset {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    margin: 0;
    padding: 0;
    margin-bottom: 10px;
}


.fk-tab {
    margin-bottom: -1px;
    text-align: center;
    padding-bottom: 10px;
    padding-top: 20px;
}

.fk-tab-link {
    background-color: transparent;
    cursor: pointer;
    padding-right: 2rem;
    padding-left: 0.5rem;
    font-size: 0.975rem;
    font-weight: 700;
    line-height: 1rem;
    position: relative;
    text-transform: uppercase;
    padding-top: 20px;
    white-space: nowrap;
}

.fk-tabset .fk-tab {
    margin-bottom: -1px;
    text-align: center;
    position: relative;
}

.fk-tabset .fk-active::before{
    position: absolute;
    content: '';
    width: 100%;
    border-radius: 3px;
    bottom: -2px;
    left: 0;
    
}

.fk-tab-element {
    padding:10px;
}