@import '../utilities/mixins';
@import '../utilities/map';

@mixin display() {
    @each $section, $value in $display-font-sizes {
        @each $section2, $value2 in $display-weight {
            .fk-display-#{$section}-#{$section2} {
                font-size: $value;
                font-weight: $value2;
                line-height: 1.2;
            }
        }
    }
}

@mixin font-weight() {
    @each $section, $value in $display-weight {
        .fk-weight-#{$section} {
            font-weight: $value;
        }
    }
}


@mixin set-position() {
    @include loopArray($positions) using($i) {
        .fk-#{nth( $positions, $i )} {
            position: nth($positions, $i);
        }
    }
}

@mixin set-display() {
    @include loopArray($displays) using($i) {
        .fk-#{nth( $displays, $i )} {
            display: nth($displays, $i);
        }
    }
}

@mixin set-overflow() {
    @include loopArray($overflow) using($i) {
        .fk-overflow-#{nth( $overflow, $i )} {
            overflow: nth( $overflow, $i);
        }
    }
}

@mixin set-cursor() {
    @include loopArray($cursor) using($i) {
        .fk-#{nth( $cursor, $i )} {
           cursor: nth($cursor, $i);
        }
    }
}

@mixin set-visibility() {
    @include loopArray($visibility) using($i) {
        .fk-visibility-#{nth( $visibility, $i )} {
            visibility: nth($visibility, $i);
        }
    }
}

