
table {
    border-collapse: collapse;
  }
  
.fk-table {
  
    width: 100%;
    margin-bottom: 1rem;
    background-color: transparent;
  
    thead tr {
       
        
        th {
            padding:.75rem
        }

    }

    tbody tr {

        padding:.75rem;
        
        td {
            padding:.75rem;

        }
    }
  
  
}


.fk-table-bordered {

    tbody tr {

        td {
            padding:.75rem;
        }
    }

}

.fk-table-full-bordered {

    tbody tr {

        td {
            padding:.75rem;
        }
    }
}


.fk-table-container {
    overflow-y: auto;
  
  
    th {
       position: sticky;
       top: 0;
       z-index: 2;
    }
}

   
.fk-header-sticky { 
    
    thead  th {
        border-top: none !important;
        border-bottom: none !important;

    }

    tbody:nth-of-type(1) tr:nth-of-type(1) td {
        border-top: none !important;
    }

    thead tr  {
        border:none !important;
    }
             
}
        

