.fk-card {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-clip: border-box;
    border-radius: .25rem;
}

.fk-card-body {
  flex: 1 1 auto;
  padding: 0.7rem;
}

.fk-card-footer {
  padding: 10px 10px;
}

.fk-card-header {
  padding: 15px 10px; 
  font-weight: 700
}
