///Theme for component menu.
///
///@author Federico Gambardella<federico.gambardella@micegroup.it>
///@param $section
///@param $map
@mixin component-menu($section, $map) {
  @if($section == 'menu') {
   .#{$section} {
      li {
          color: map-get($map, li-color);
          border-bottom: map-get($map, li-border-bottom);

          &:hover {
            color: map-get($map, li-hover-color)
          }
          &:focus {
            color: map-get($map, li-hover-focus)
          }
          &.active {
            border-color: map-get($map, li-active) !important;
            border-bottom: 1px solid map-get($map, li-active );
            border-top: 1px solid map-get($map, li-active );
          }
      }

      .icon-margin {
         color: map-get($map, icon-margin)
      }
    }
  }
}
