@import '../utilities/mixins';

///Theme for component spinner.
///
///@author Federico Gambardella<federico.gambardella@micegroup.it>
///@param $section
///@param $map
@mixin component-spinner($section, $map) {
    @if($section == 'spinner') {
        @include spinner-color('basic',$map);
        @include spinner-color('primary',$map);
        @include spinner-color('danger',$map);
    }
}

/// Set button spinner.
/// 
/// @author Federico Gambardella<federico.gambardella@micegroup.it>
/// @param $name
/// @param $map
@mixin spinner-color($name, $map) {
   .fk-spinner-#{$name} {
       stroke:  map-get($map, stroke-#{$name})
   }
}