///Theme for component button.
///
///@author Federico Gambardella<federico.gambardella@micegroup.it>
///@param $section
///@param $map
@mixin component-badge($section, $map) {
    @if($section == 'badge') {
        @include badge-color('basic',$map);
        @include badge-color('primary',$map);
        @include badge-color('danger',$map);
    }
}


/// Set button color.
/// 
/// @author Federico Gambardella<federico.gambardella@micegroup.it>
/// @param $name
/// @param $map
@mixin badge-color($name, $map) {
    .fk-badge-#{$name} {
        background: map-get($map, bg-#{$name});
    }
}