//Theme for component pagination.
///
///@author Federico Gambardella<federico.gambardella@micegroup.it>
///@param $section
///@param $map
@mixin component-pagination($section, $map) {
    @if($section == 'pagination') {

        .fk-page-link {
            color: map-get($map, button-color);
            background-color: map-get($map, button-bg);
            border: map-get($map, button-border);
        }
        
        .fk-active-page {
            background-color: map-get($map, button-active-bg);
            color: map-get($map, button-active-color);
        }
        
    }
  }
  

  







