/// Theme for stepper component.
///
/// @author Federico Gambardella<federico.gambardella@micegroup.it>
/// @param $section
/// @param $map
@mixin component-stepper($section, $map) {
    @if($section == 'stepper') {
        .fk-stepper {

            .fk-step-trigger {
                color: map-get($map, step-trigger);
            }

            .fk-active {
                
                .fk-stepper-circle {
                    color:map-get($map, circle-active-color);
                    border-color:map-get($map, circle-active-border-color);
                }
            }

            .fk-line {
                background-color:map-get($map, line-bg);
            }
        }

        .fk-stepper-circle {
            color:map-get($map, circle-color);
            border: map-get($map, circle-border);
        }

        .fk-stepper-line {
            background-color: map-get($map, line-bg);
        }

        .fk-complited {
            background-color:map-get($map, step-completed-bg);
            color:map-get($map,step-completed-color)!important;
            border-color: map-get($map, step-completed-border-color);
        }
        
        .fk-navigate {
            background-color:map-get($map, navigate-bg)!important;
        }

    }

}
