//display
$display-font-sizes: (1: 5rem, 2: 4.5rem, 3: 4rem, 4: 3.5rem, 5: 3rem, 6: 2.5rem, 7: 2rem, 8: 1.5rem, 9: 1rem);
$display-weight: ( 1: 100, 2: 200, 3: 300, 4: 400, 5: 500, 6: 600, 7: 700, 8: 800, 9: 900 );
//viewport-size
$view-port-width: (1: 10vw, 2:20vw,3: 30vw, 4: 40vw, 5: 50vw, 6: 60vw, 7: 70vw, 8: 80vw, 9: 90vw, 10: 100vw);
$view-port-height: (1: 10vh, 2:20vh,3: 30vh, 4: 40vh, 5: 50vh, 6: 60vh, 7: 70vh, 8: 80vh, 9: 90vh, 10: 100vh);
//percentage-size
$percentage-size: (1: 10%, 2:20%,3: 30%, 4: 40%, 5: 50%, 6: 60%, 7: 70%, 8: 80%, 9: 90%, 10: 100%);
//positions
$positions: (static, relative, fixed, absolute, sticky);
//display
$displays: (none, inline, block, inline-block);
//overflow
$overflow:(visible, hidden, scroll, auto);
//cursor
$cursor:(pointer, auto, help, default, none, context-menu, progress, wait, cell, crosshair, text, 
vertical-text, alias, copy, move, no-drop, not-allowed, grab, grabbing, all-scroll, col-resize, row-resize);
//visibility
$visibility:(visible, hidden, collapse);
